<script>
import anime from "animejs/lib/anime.es.js";

export default {
  name: "App",
  components: {},
  data() {
    return {
      secondRow: false,
      page: "main",
    };
  },
  mounted() {
    anime({
      targets: ".mainRow",
      opacity: 1,
      marginBottom: 0,
      duration: 1000,
      delay: 200,
      easing: "easeOutQuad",
    });
  },
  methods: {
    openCalendly() {
      window.open("https://calendly.com/drtns/30min", "_blank");
    },
    downloadCV() {
      window.open("/docs/cv_en.pdf", "_blank");
    },
    openTG() {
      window.open("tg:@destes", "_blank");
    },
    showSecondRow(ref) {
      anime({
        targets: ".page",
        opacity: 0,
        duration: 600,
        easing: "easeOutQuad",
        complete: () => {
          this.page = ref;

          const $ref = this.$refs[ref];
          $ref.style.display = "block";

          anime({
            targets: $ref,
            opacity: 1,
            duration: 600,
            easing: "easeOutQuad",
            complete: () => {},
          });
        },
      });
    },
  },
};
</script>

